import './_components.js';




 // Инициализация миниатюр
 const thumbsSwiper = new Swiper('.swiper-thumbs', {
  direction: 'vertical', // Вертикальное расположение миниатюр
  slidesPerView: 3, // Количество миниатюр
  spaceBetween: 10, // Отступы между миниатюрами
  watchSlidesVisibility: true,
  watchSlidesProgress: true,
});

// Инициализация основного слайдера
const mainSwiper = new Swiper('.main-slider', {
  slidesPerView: 1,
  spaceBetween: 10,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  thumbs: {
    swiper: thumbsSwiper, // Привязка миниатюр
  },
});




// Инициализация миниатюр
const secondThumbs = new Swiper('.second-thumbs', {
  direction: 'vertical', // Вертикальное расположение миниатюр
  slidesPerView: 3, // Количество миниатюр
  spaceBetween: 10, // Отступы между миниатюрами
  watchSlidesVisibility: true,
  watchSlidesProgress: true,
});


// Инициализация основного слайдера
const secondSwiper = new Swiper('.second-slider', {
  slidesPerView: 1,
  spaceBetween: 10,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  thumbs: {
    swiper: secondThumbs, // Привязка миниатюр
  },
});

